$font-size: 1rem;
$font-color: lch(28 99 35);

html {
  font-size: $font-size;
  color: $font-color;
}

* {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: StratosSkyeng;
  src: url('stratosskyengweb-regular.woff');
  src: url('stratosskyengweb-regular.woff2');
}

.center {
  padding-left: calc(50% - 490px);
  padding-right: calc(50% - 490px);
}

a {
  text-decoration: none;
}

.body {
  font-family: StratosSkyeng, sans-serif;
  background-color: #004980;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-weight: normal;
  font-style: normal;
}

.game {
  background-color: #c2f5ff;
  width: 480px;
  height: 459px;
  border-radius: 12px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 48px;
  margin-top: 158px;

  &__level {
      font-style: normal;
      font-weight: 400;
      font-size: 40px;
      line-height: 48px;
      text-align: center;
      color: #004980;

      &_button {
          font-family: StratosSkyeng, sans-serif;
          height: 98px;
          width: 98px;
          background-color: white;
          border-radius: 12px;
          font-style: normal;
          font-weight: 400;
          font-size: 64px;
          line-height: 72px;
          color: #0080c1;
          border-style: none;
          transition: all 0.3s;
      }

      &_button:hover {
          box-shadow: 0 0 7px #0080c1;
      }

      &_buttons {
          display: flex;
          gap: 26px;
      }
  }

  &__start {
      font-family: StratosSkyeng, sans-serif;
      margin-top: 16px;
      height: 50px;
      width: 246px;
      background: #7ac100;
      border-radius: 12px;
      border-style: none;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 32px;
      color: white;
      transition: all 0.3s;
  }

  &__start:hover {
      transform: scale(1.05);
  }

  &__card {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      flex-wrap: wrap;
      gap: 21px;
      margin-top: 32px;
  }
}

.focus {
  border: 3px solid #0080c1;
  box-shadow: 0 0 0;
}

.focus:hover {
  box-shadow: 0 0 0;
}

.card {
  height: 133px;
  width: 95px;
  background-image: 'static/shirt.png';
}

