.gameprocess {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 22px;

  &__stopwatch {
      &_timer {
          font-style: normal;
          font-weight: 400;
          font-size: 64px;
          line-height: 72px;
          color: #fff;
      }

      &_units {
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 32px;
          color: #fff;
          display: flex;
          justify-content: flex-end;
          margin-bottom: -10px;
      }

      &_units-sek {
          margin-left: 53px;
          margin-right: 3px;
      }
  }

  &__button_start {
      width: 246px;
      height: 48px;
      background-color: #7ac100;
      color: #fff;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 32px;
      border-style: none;
      border-radius: 12px;
      transition: all 0.3s;
      margin-top: 16px;
  }

  &__button_start:hover {
      transform: scale(1.05);
  }
}

.hidden {
  display: none;
}

.field {
  font-family: StratosSkyeng, sans-serif;
  background-color: #004980;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-weight: normal;
  font-style: normal;

  &__card {
      display: grid;
      grid-template-columns: repeat(9, 1fr);
      flex-wrap: wrap;
      gap: 21px;
      margin-top: 32px;
  }
}

.hidden {
  display: none;
}

.flip {
  transform: rotateY(180deg);
}